@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100;300&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background: #f5f5f5;
  display: flex;
  align-items: center;
  font-family: 'Roboto', Helvetica, asans-serif;
  justify-content: center;
}

/* ToDos */

.todos-container {
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
  margin: 3rem 0;
  max-width: 40rem;
  width: 90vw;
}

h1 {
  color: #5d70ea;
  font-size: 6rem;
  font-weight: 100;
  margin: 5rem 0;
  text-align: center;
}

form input {
  border: 1px solid #eee;
  box-sizing: border-box;
  color: #333;
  font-family: 'Roboto';
  font-size: 1.3rem;
  font-weight: 300;
  margin: 0;
  padding: 1.5rem;
  outline: none;
  width: 100%;
}

form input::placeholder {
  color: #ccc;
}

/* ToDo */

article {
  align-items: center;
  background: #fff;
  border-bottom: 1px solid #eee;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  padding: 1.5rem;
}

article:hover .remove {
  display: block;
}

.description-wrapper {
  align-items: center;
  display: flex;
}

article h4 {
  color: #333;
  font-size: 1.3rem;
  font-weight: 300;
  margin-left: 1.5rem;
  line-height: 1.4;
}

article .remove {
  cursor: pointer;
  display: none;
  transition: all 500ms ease-in-out;
}

.circle {
  align-items: center;
  border: 1px solid #5d70ea;
  border-radius: 25px;
  cursor: pointer;
  display: flex;
  height: 25px;
  justify-content: center;
  width: 25px;
}

.circle:hover {
  background: #e1e5ff;
}

.checked {
  background: #5d70ea;
  border-radius: 15px;
  height: 15px;
  width: 15px;
}

.completed {
  color: #ccc;
  text-decoration: line-through;
}

.remove {
  height: 17px;
  width: 17px;
}

/* Filters */

.filters-container {
  align-items: center;
  background: #f5f5f5;
  border-left: 1px solid #eee;
  border-right: 1px solid #eee;
  display: flex;
  justify-content: space-between;
  padding: 1.5rem;
  overflow: hidden;
  outline: none;
}

.remaining {
  color: #333;
  font-size: 0.9rem;
}

.filter {
  background: #ddd;
  border: none;
  border-radius: 3px;
  box-sizing: border-box;
  color: #000;
  cursor: pointer;
  font-family: 'Roboto';
  font-size: 0.8rem;
  margin: 0 0.3rem;
  outline: none;
  padding: 0.3rem 0.6rem;
  transition: all 200ms ease-in-out;
}

.filter:hover {
  background: #5d70ea;
  color: #fff;
}

.active {
  background: #5d70ea;
  color: #fff;
}

.clear-completed {
  background: none;
  border: none;
  color: #333;
  cursor: pointer;
  font-family: 'Roboto';
  font-size: 0.8rem;
  outline: none;
}

.clear-completed:hover {
  text-decoration: underline;
}
